<template>
  <div class="rightContent">
    <div class="navTile">
      当前位置：
      <router-link :to="{name:'Main'}">首页</router-link>
      &nbsp;>>&nbsp;
      <router-link :to="{name:'Center'}">学生中心</router-link>
      &nbsp;>>&nbsp;
      <router-link :to="{name:'CenterList',query:{typeName,index}}">{{menuName}}</router-link>
    </div>
    <div class="content">
      <div class="myDetail">
        <div class="videoTitle">
          <p class="videoP">{{file.courseName}}</p>
          <div class="videoSpanDiv">
            <span class="videoSpan">
            {{file.courseTime}}
          </span>
            <span class="videoSpan">
            {{file.viewTimes}}人浏览
          </span>
          </div>
        </div>
        <div class="videoContent">
          <div id="introduce" class="introduce">
            {{setIntroduce('introduce',file.content)}}
          </div>
        </div>
        <div class="videoComment">
          <div class="videoCommentItem">
            <div class="videoCommentTitle">
              我要评论：
            </div>
            <div class="videoCommentForm">
              <el-form ref="form" :model="commentform" label-width="60px">
                <el-form-item label="姓名：">
                  <el-input v-model="commentform.commentName" placeholder="请输入姓名"></el-input>
                </el-form-item>
                <el-form-item label="内容：">
                  <el-input type="textarea"
                            v-model="commentform.commentContent"
                            :autosize="{ minRows: 3, maxRows: 4}"
                            maxlength="100"
                            show-word-limit>
                  </el-input>
                </el-form-item>
                <el-form-item>
                  <el-button type="primary" @click="uploadComment">发表评论</el-button>
                </el-form-item>
              </el-form>
            </div>
          </div>
          <div class="videoCommentItem">
            <div class="videoCommentTitle">
              评论列表：
            </div>
            <div class="videoComments" v-if="comments.commentVOS && comments.commentVOS.length > 0">
              <ul class="commentUl" v-for="(comment,index) in comments.commentVOS" :key="index">
                <li class="commenterInfo">
                  <span class="commenterName">{{comment.commentName}}：</span>
                  <span class="commenterTime">{{comment.commentTime}}</span>
                </li>
                <li class="comment">
                  {{comment.commentContent}}
                </li>
              </ul>
              <div class="page">
                <el-pagination
                  @current-change="handleCurrentChange"
                  :current-page.sync="pageNum"
                  :page-size="pageSize"
                  :hide-on-single-page="comments.pages < 2"
                  layout="total, prev, pager, next"
                  :total="comments.total">
                </el-pagination>
              </div>
            </div>
            <div class="videoComments emptyComment" v-else>
              快来抢沙发吧
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {mapActions} from 'vuex'
  export default {
    name: '',
    data () {
      return {
        List:[],                 //数据数组
        menus:[],                //目录数组
        menuName:'',             //菜单名
        typeName:'Center',       //当前菜单栏选项
        index:0,                 //第几个小菜单

        file:{},                 //当前显示文件

        videoId:'',            //视频Id
        pageSize:10,           //评论行数
        pageNum:1,             //评论页数
        videos:[],             //视频列表
        video:{},              //当前视频对象

        commentform:{
          commentName:'',      //评论者姓名
          commentContent:'',   //评论内容
        },
        comments:[],           //评论数组
      }
    },
    methods: {
      ...mapActions(['choiceMenuId','choiceTypeMenuId']),

      //分页
      handleCurrentChange(num){
        this.pageNum = num
      },

      //发表评论
      async uploadComment(){
        var {videoId} = this
        var {commentName,commentContent} = this.commentform
        if(commentName.trim() && commentContent.trim() && videoId){
          var typeId = videoId
          var result = await UPLOADCOMMENT({commentName,commentContent,typeId})
          if(result && result.status === 0){
            this.commentform.commentName = ''
            this.commentform.commentContent = ''
            var message = '发表成功，等待审核'
            this.message(message,'success',1500)
          }
        }else{
          this.message('请输入完整信息','warning',1500)
        }
      },

      //显示视频介绍
      setIntroduce(id,videoIntro){
        $('#'+id).html(videoIntro)
      },

      //更新数据显示
      handleId(){
        var {List,menus,typeName,index,id} = this
        this.file = List[id]
        this.menuName = menus[index].menuName
        this.choiceMenuId(typeName)
        this.choiceTypeMenuId(typeName+index)
      },

      //消息提示
      message(message,type,duration){
        this.$message({
          message,
          type,
          duration
        })
      }
    },
    watch: {
      //深度监听地址栏参数变化，一变化就切换对应的数据
      $route: {
        handler() {
          this.id = this.$route.query.id * 1
          this.handleId()
        },
        deep: true,
      }
    },
    mounted () {
      //页面重新加载时获取地址栏参数显示对应数据
      if(this.$route.query.typeName){
        this.typeName = this.$route.query.typeName
        this.index = this.$route.query.index * 1
        this.id = this.$route.query.id * 1
      }
      //当前栏目下的子菜单
      this.menus = [
        {
          typeName:'Center',
          menuName:'16级学生课程报告',
          name:'CenterDetail'
        },
        {
          typeName:'Center',
          menuName:'17级学生课程报告',
          name:'CenterDetail'
        },
        {
          typeName:'Center',
          menuName:'18级学生课程报告',
          name:'CenterDetail'
        },
        {
          typeName:'Center',
          menuName:'19级学生课程报告',
          name:'CenterDetail'
        },
      ]
      //当前栏目下的各个菜单包含的数据
      this.List = [
        {
          "id": "0",
          "courseName": "第一组实验报告",
          "courseTime": "2020-09-28",
          "viewTimes": 22,
          "content":'第一组实验报告：啊啊啊啊',
        },
        {
          "id": "1",
          "courseName": "第二组实验报告",
          "courseTime": "2020-09-28",
          "viewTimes": 22,
          "content":'第二组实验报告：啊啊啊啊',
        },
        {
          "id": "2",
          "courseName": "第三组实验报告",
          "courseTime": "2020-09-28",
          "viewTimes": 22,
          "content":'第三组实验报告：啊啊啊啊',
        },
        {
          "id": "3",
          "courseName": "第四组实验报告",
          "courseTime": "2020-09-28",
          "viewTimes": 22,
          "content":'第四组实验报告：啊啊啊啊',
        },
        {
          "id": "4",
          "courseName": "第五组实验报告",
          "courseTime": "2020-09-28",
          "viewTimes": 22,
          "content":'第五组实验报告：啊啊啊啊',
        },
        {
          "id": "5",
          "courseName": "第六组实验报告",
          "courseTime": "2020-09-28",
          "viewTimes": 22,
          "content":'第六组实验报告：啊啊啊啊',
        },
        {
          "id": "6",
          "courseName": "第七组实验报告",
          "courseTime": "2020-09-28",
          "content":'第七组实验报告：啊啊啊啊',
        },
        {
          "id": "7",
          "courseName": "第八组实验报告",
          "courseTime": "2020-09-29",
          "viewTimes": 22,
          "content":'第八组实验报告：啊啊啊啊',
        },
      ]
      //初始化数据
      this.handleId()
    },
  }
</script>

<style lang="less">
  .userResource{
    .rightContent{
      .navTile{
        padding-left: 40px;
        height: 45px;
        border-radius: 5px 5px 0 0;
        line-height: 45px;
        color: #fff;
        font-size: 14px;
        background: rgb(85,171,235);
        overflow: hidden;
        a{
          color: white;
          text-decoration: none;
          &:hover{
            text-decoration: underline;
          }
        }
      }
      .content{
        line-height: 24px;
        padding: 10px 15px 15px;
        border: 1px solid #CECECE;
        border-top: 0;
        .myDetail{
          .videoTitle{
            text-align: center;
            margin:0;
            .videoP{
              font-size: 18px;
              font-weight: bold;
              color: #333333;
              line-height: 30px;
              margin-bottom: 0;
              margin-top: 0px;
            }
            .videoSpanDiv{
              line-height: 30px;
              border-bottom: 1px solid rgb(233,233,233);
              text-align: center;
              padding-bottom: 10px;
              margin-bottom: 0px;
              .videoSpan{
                display: inline-block;
                width: 100px;
                font-size: 12px;
                color: rgb(100,100,100);
              }
            }
          }
          .videoContent{
            min-height: 150px;
            .videoContentTitle{
              font-size: 18px;
              font-weight: bold;
              color: #333333;
              margin-top:20px;
            }
            .introduce{
              text-indent: 2em;
              padding: 15px;
              color: #333;
              font-size: 14px;
              line-height: 23px;
              min-height: 100px;
            }
          }
          .videoComment{
            .videoCommentItem{
              margin-top:35px;
              .videoCommentTitle{
                font-size: 18px;
                font-weight: bold;
                color: #333333;
                padding-bottom: 15px;
                border-bottom: 1px solid rgb(233,233,233);
              }
              .videoCommentForm{
                margin-top: 30px;
              }
              .videoComments{
                min-height: 200px;
                ul.commentUl{
                  padding-left: 0px;
                  padding-bottom: 20px;
                  border-bottom: 1px solid rgb(233,233,233);
                  margin: 10px 0;
                  li{
                    list-style: none;
                    padding: 0;
                    margin: 0;
                  }
                  .commenterInfo{
                    line-height: 30px;
                    font-size: 14px;
                    padding: 10px 5px;
                    &:before,&:after{
                      content: '';
                      display: table;
                      clear: both;
                    }
                    .commenterName{
                      display: inline-block;
                      float: left;
                    }
                    .commenterTime{
                      display: inline-block;
                      float: right;
                    }
                  }
                  .comment{
                    padding-left: 2em;
                    line-height: 30px;
                    font-size: 14px;
                    color: #333;
                    max-height: 120px;

                    /*最多显示四行内容，多出来的显示为...*/
                    overflow: hidden;
                    text-overflow: ellipsis;
                    display: -webkit-box;
                    -webkit-line-clamp: 4;
                    -webkit-box-orient: vertical;
                  }
                }
              }
              .emptyComment{
                padding: 30px 0 0;
                line-height: 30px;
                text-align: center;
                font-size: 20px;
                color: rgb(211,211,211);
              }
            }
          }
        }
      }
    }
  }
  @media screen and (max-width: 767px){
    .userResource{
      .rightContent{
        .navTile{
          padding-left: 10px;
        }
        .content{
          padding-top: 5px;
          .myDetail{
            .videoContent{
              min-height: 150px;
              .introduce{
                padding: 15px 0;
              }
            }
          }
        }
      }
    }
  }
</style>



